/* Single Post Excerpt positioning on Thumbnail image */
.post-thumbnail__excerpt {
	font-weight: 900;
	text-align: center;
	text-shadow: 0 0 2px gray;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
