	font-size: 28px;
	line-height: 1.2;
	color: white;
	padding-left: 1em;
	padding-right: 1em;
	margin: 0;
	position: relative;
}
@media screen and (min-width: 320px) {
	.post-thumbnail__excerpt p {
		font-size: calc(28px + 62 * (100vw - 320px) / 880);
	}
}
@media screen and (min-width: 1200px) {
	.post-thumbnail__excerpt p {
		font-size: 90px;
	}
}
