		max-width: 960px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		padding-left: 1rem;
		padding-right: 1rem;
	}
}
.post-thumbnail__excerpt p::after {
	content: attr(data-text);
	opacity: 0;
	position: absolute;
	left: 2px;
	text-shadow: -1px 0 rgb(255, 255, 255);
	top: 0;
	color: white;
	padding-left: 1em;
	padding-right: 1em;
	overflow: hidden;
}
@media screen and (min-width: 768px) {
	.post-thumbnail__excerpt p::after {
		max-width: 960px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		padding-left: 1rem;
		padding-right: 1rem;
	}
}
.post-thumbnail__excerpt p::before {
	content: attr(data-text);
	opacity: 0;
	position: absolute;
	left: -2px;
	text-shadow: 1px 0 rgb(0, 0, 0);
	top: 0;
	color: white;
	padding-left: 1em;
	padding-right: 1em;
	overflow: hidden;
}
@media screen and (min-width: 768px) {
