/*--------------------------------------------------------------
## Placeholders
--------------------------------------------------------------*/
/* Placeholder loader */
#placeholder__content,
#frontpage__loader {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	margin-top: 2em;
	padding-left: 1em;
	padding-right: 1em;
