#frontpage__loader.action {
	opacity: 0;
}
@media screen and (min-width: 768px) {
	#placeholder__content,
#frontpage__loader {
		padding: 0;
	}
}

.loader-placeholder {
	display: none;
	position: relative;
	background: #fff;
	padding: 1em 1em 1.5em;
	margin-bottom: 1em;
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
	overflow: hidden;
}
@media screen and (min-width: 768px) {
	.loader-placeholder {
		padding: 1.5em 1em 2em;
		margin-bottom: 2em;
	}
}
.loader-placeholder div {
	background-color: #ced4da;
	overflow: hidden;
}
.loader-placeholder div:after {
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 50%;
	z-index: 1;
	width: 500%;
	height: 100%;
	margin-left: -250%;
	animation: placeholder-animation 1s linear infinite;
	background: linear-gradient(to right, rgba(255, 255, 255, 0) 46%, rgba(255, 255, 255, 0.35) 50%, rgba(255, 255, 255, 0) 54%) 50% 50%;
}
.loader-placeholder div:nth-child(1) {
	height: 1.6em;
	width: 60%;
	margin: 0.3em 0 0.4em;
}
@media screen and (min-width: 768px) {
	.loader-placeholder div:nth-child(1) {
		height: 2em;
		margin: 0.5em 0 0.6em;
	}
}
.loader-placeholder div:nth-child(2) {
	height: 0.8em;
	width: 20%;
}
@media screen and (min-width: 768px) {
	.loader-placeholder div:nth-child(2) {
		height: 0.5em;
	}
}
.loader-placeholder div:nth-child(3) {
	height: 4.8em;
	width: 100%;
	margin-top: 0.3em;
}
@media screen and (min-width: 768px) {
	.loader-placeholder div:nth-child(3) {
		height: 1.2em;
	}
}

/* Content placeholders animation*/
@keyframes placeholder-animation {
	0% {
		transform: translate3d(-30%, 0, 0);
