/*--------------------------------------------------------------
## Dropdown
--------------------------------------------------------------*/
.dropdown-wrapper {
	display: inline-block;
	position: relative;
}

button.dropdown-button {
	background-color: transparent;
	border: none;
	padding: 0;
	cursor: pointer;
}
button.dropdown-button .icon {
	fill: #000;
	width: 25px;
	height: 25px;
}

.dropdown-menu__wrapper {
	display: none;
	position: absolute;
	left: 50%;
	margin-top: 5px;
	transform: translateX(-50%);
}
.dropdown-menu__wrapper.visible {
	display: block;
}
.dropdown-menu__wrapper[data-popper-placement^=top] > .dropdown-menu-tip {
	bottom: -7px;
}
.dropdown-menu__wrapper[data-popper-placement^=bottom] > .dropdown-menu-tip {
	top: -7px;
}
.dropdown-menu__wrapper[data-popper-placement^=left] > .dropdown-menu-tip {
	right: -7px;
}
.dropdown-menu__wrapper[data-popper-placement^=right] > .dropdown-menu-tip {
	left: -7px;
}

.dropdown-menu {
	background: #fff;
	border: 1px solid rgba(0, 0, 0, 0.1);
	border-radius: 4px;
	box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 4px;
}
.dropdown-menu ul {
	margin: 0;
	padding: 8px 0;
	list-style: none;
}
.dropdown-menu li {
	text-align: left;
	line-height: 1;
	white-space: nowrap;
}
.dropdown-menu li a {
	font-size: 16.02px;
	font-size: 1.0235rem;
	color: #555d66;
	padding: 8px 20px;
	display: block;
}
.dropdown-menu li a:hover {
	text-decoration: none;
	color: #222;
}

.dropdown-menu-tip {
	position: absolute;
}
.dropdown-menu-tip:before {
	content: "";
	background: #fff;
	display: block;
	height: 14px;
	width: 14px;
	box-shadow: rgba(0, 0, 0, 0.54) -1px -1px 1px -1px;
	transform: rotate(45deg);
}

.dropdown-menu__wrapper[data-popper-placement^=top] > .dropdown-menu-tip:before {
	transform: rotate(223deg);
}